import tumult from 'tumult';

import Engine from './utils/engine';
// import Engine from './utils/engine3d';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const noise = new tumult.Perlin2();

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

function drawCircle(cx, cy, size, padding = 0, scale = 1) {
  ctx.save();
  ctx.translate(cx, cy);
  ctx.scale(scale + 0.5, scale + 0.5);
  ctx.beginPath();
  ctx.arc(0, 0, (size / 2) - (padding / 2), 0, Math.PI * 2)
  ctx.stroke();
  ctx.restore();
}

let rows = 20;
let cols = 20;

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  let size = width / cols;

  ctx.save();
  ctx.translate(size / 2, size / 2);
  for (let x = 0; x < cols; x++) {
    for (let y = 0; y < rows; y++) {
      let noise1 = noise.gen((x + (frame / 10)) / 100, (y + (frame / 10)) / 100);
      let noise2 = noise.gen((20 + (x + (frame / 10)) / 10), 20 + ((y + (frame / 10)) / 10));

      ctx.strokeStyle = `hsl(${ noise1 * 360 }, 100%, 50%)`;
      drawCircle(x * size, y * size, size, 10, noise2);
    }
  }
  ctx.restore();
});

app.start();
